<template>
  <div>
    <page-loading v-if="loading"></page-loading>
    <div v-if="!loading">
      <div class="row justify-content-center">
        <div class="col-md-6">
          <vx-card>
            {{ writing.question}}
          </vx-card>
        </div>
      </div>
      <div class="row justify-content-center mt-8">
        <div class="col-md-6">
          <vx-card>
            {{ writing.answer}}
          </vx-card>
        </div>
      </div>
      <div class="row justify-content-center mt-8">
        <div class="col-md-6">
          <vx-card>
            {{ writing.sample}}
          </vx-card>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import mixins from '../../../mixins'
import axios from '../../../http/axios'

export default {
  name: 'WritingResult',
  mixins: [mixins],
  data () {
    return {
      loading: this,
      writing: null,
      writingId: this.$route.query.writingId
    }
  },

  mounted () {
    this.getWriting()
  },

  methods: {
    getWriting() {
      this.loading = true;

      this.writings = [];
      let url = `/api/english/writings/${this.writingId}`
      axios.get(url, {
        headers: {
          Accept: "application/json",
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        if (res.status === 200) {
          console.log(res);
          this.writing = res.data;
        } else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger');
        }
        this.loading = false;

      })
    }
  }
}
</script>

